import { Observable } from 'rxjs/Rx';
import { LoadingService } from '../../services/loading.service';
import { Profile } from '../../models/Profile';
import { TransferHttp } from '../../../modules/transfer-http/transfer-http';
import { Component, OnInit } from '@angular/core';

@Component({
	selector: 'about-view',
	templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutView implements OnInit {
  public profiles : Profile[][] = [];
  public load = true
  public rows:number[]
  public subscription :any
  constructor(private http: TransferHttp, private loading: LoadingService) {
  }
  ngOnInit() {
    this.subscription = this.loading.get().subscribe(status=>this.load = status)
    this.http.get('https://maxang-119e0.firebaseio.com/users.json').map(data=>{
            return data
        }).subscribe(data=>{
            this.loading.off()
            for(let i=0;i<(Object.keys(data).length/3);i++)
              this.profiles[i] = [data[i*3], data[(i*3)+1], data[(i*3)+2]]
    });
  }
  ngOnDestroy() {
    this.subscription.unsubscribe()
    this.loading.on()
  }
}



